@use "../../styles/partials/palette";
@use "../../styles/partials/mixin";

.Header {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 1rem;

	&__Heading {
		text-align: center;
		font-size: 4rem;
		font-family: "Playfair Display", serif;
		color: palette.$primary-colour;

		@include mixin.phone {
			font-size: 2rem;
		}
	}

	&__Text {
		font-weight: bold;
		text-align: center;
		font-size: 2.5rem;
		font-family: "Playfair Display", serif;
		color: palette.$primary-colour;

		@include mixin.phone {
			font-size: 1.5rem;
		}
	}
}
