@use "../../styles/partials/palette";
@use "../../styles/partials/typography";

.Card {
	display: flex;
	flex-direction: row;
	overflow: hidden;
	align-items: center;
	justify-content: space-between;
	width: 350px;
	height: 200px;
	margin: 0.5rem;
	background-color: palette.$secondary-colour-80;
	backdrop-filter: blur(20px);
	border-radius: 10px;
	user-select: none;

	&:hover {
		background-color: palette.$secondary-colour;
		cursor: pointer;
	}

	&__Content {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	&__Image {
		display: flex;
		align-items: flex-end;
		width: 80px;
		height: 200px;
		padding: 1rem;
		background-color: palette.$secondary-colour;
		object-fit: scale-down;
		user-select: none;
	}

	&__Title {
		font-size: 1rem;
		padding: 0rem 1rem;
		font-family: typography.$playfair;
		color: palette.$main-colour;
	}

	&__Author {
		text-decoration: wavy;
		font-size: 0.8rem;
		padding: 0rem 1rem;
		margin-top: 0.5rem;
		color: palette.$main-colour;
		font-style: italic;
	}

	&__Description {
		font-size: 0.8rem;
		padding: 0.5rem 1rem;
		color: palette.$main-colour;
	}
}
